import utils from '../utils/utils';

const initMap = (mapElement, data) => {
    // console.log(data)
    const map = new google.maps.Map(mapElement, {
        center: new google.maps.LatLng(data.center[0], data.center[1]),
        zoom: 10
    });

    data.places.forEach((el, i) => {
        const marker = new google.maps.Marker({
            position: new google.maps.LatLng(el[0], el[1]),
            icon: '/img/images/marker.png',
            map: map
        });
    })
}

const contactsMap = utils.getElem('.js-contacts-map');

if(contactsMap){
    // console.log(JSON.stringify(mapData))
    google.maps.event.addDomListener(window, 'load', e =>{
        initMap(contactsMap, JSON.parse(contactsMap.dataset.map))
    });
}


