import slick from "slick-carousel";
import utils from "../utils/utils";

const sliderOpts = {
  slidesToScroll: 1,
  dots: true,
  arrows: false,
  autoplay: true
  // fade: true
};

const loyaltySlider = $(".js-loyalty-slider");
const loyaltySliderOpts = {
  slidesToShow: 2,
  slidesToScroll: 2,
  dots: true,
  autoplay: true,
  appendArrows: $(".js-slider-loyalty-controls"),
  responsive: [
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 1,
        arrows: false
      }
    }
  ]
};
loyaltySlider.slick(loyaltySliderOpts);

const loyalSlider = $(".js-loyal-slider");
const loyalSliderOpts = {
  slidesToShow: 2,
  slidesToScroll: 2,
  dots: true,
  autoplay: true,
  appendArrows: $(".js-slider-loyal-controls"),
  autoplaySpeed: 5000,
  appendDots: $(".js-slider-loyal-controls"),
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 1,
        arrows: false
      }
    }
  ]
};

loyalSlider.slick(loyalSliderOpts);

const indexReviewsSlider = $(".js-rev-slider");
const indexReviewsSliderOpts = {
  slidesToShow: 2,
  slidesToScroll: 2,
  dots: true,
  autoplay: true,
  appendArrows: $(".js-slider-rev-controls"),
  autoplaySpeed: 5000,
  appendDots: $(".js-slider-rev-controls"),
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 1,
        arrows: false
      }
    }
  ]
};

indexReviewsSlider.slick(indexReviewsSliderOpts);

const eventsSlider = $(".js-events-slider");
const eventsSliderliderOpts = {
  slidesToShow: 3,
  arrows: true,
  slidesToScroll: 3,
  appendArrows: $(".js-slider-events-controls"),
  appendDots: $(".js-slider-events-controls"),
  autoplay: true,
  autoplaySpeed: 5000,
  dots: true,
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 2
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 1,
        arrows: false
      }
    }
  ]
};

eventsSlider.slick(eventsSliderliderOpts);

const teamSlider = $(".js-team-slider");
const teamSliderliderOpts = {
  slidesToShow: 3,
  arrows: true,
  slidesToScroll: 3,
  appendArrows: $(".js-slider-team-controls"),
  appendDots: $(".js-slider-team-controls"),
  autoplay: true,
  autoplaySpeed: 5000,
  dots: true,
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 2
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 1,
        arrows: false
      }
    }
  ]
};

teamSlider.slick(teamSliderliderOpts);

const simpleSlider = $(".js-slider");
simpleSlider.slick(sliderOpts);

const descrSlider = $(".js-descr-slider");
const descrSliderOpts = Object.assign({}, sliderOpts, {
  slidesToShow: 5,
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2
      }
    },
    {
      breakpoint: 767,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]
});

descrSlider.slick(descrSliderOpts);

const orderSlider = $(".js-order-slier");
const orderSliderOpts = Object.assign({}, sliderOpts, {
  slidesToShow: 7,
  autoplay: false,
  infinite: false,
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 5
      }
    },
    {
      breakpoint: 767,
      settings: {
        slidesToShow: 3
      }
    },
    {
      breakpoint: 400,
      settings: {
        slidesToShow: 2
      }
    }
  ]
});

orderSlider.slick(orderSliderOpts);

const mainSlider = $(".js-main-slider");
mainSlider.slick(
  $.extend({}, sliderOpts, {
    arrows: true,
    autoplay: true,
    autoplaySpeed: 5000,
    fade: true
  })
);

$(".js-partners-slider").slick({
  slidesToShow: 3,
  arrows: true,
  slidesToScroll: 3,
  rows: 2,
  appendArrows: $(".js-slider-partners-controls"),
  appendDots: $(".js-slider-partners-controls"),
  autoplay: true,
  autoplaySpeed: 5000,
  dots: true,
  responsive: [
    {
      breakpoint: 992,
      settings: {
        slidesToShow: 2,
        rows: 1
      }
    },
    {
      breakpoint: 600,
      settings: {
        slidesToShow: 1,
        arrows: false,
        rows: 1
      }
    }
  ]
});

$(".js-opposition-slider").slick({
  arrows: true,
  fade: true
});

export { orderSliderOpts, sliderOpts, loyaltySliderOpts };



