import utils from '../utils/utils';

const apiReady = ()=>{
    window.YtApiReady = true;
}

const loadVideoApi = () => {
    const tag = document.createElement('script');
    tag.src = "https://www.youtube.com/iframe_api";
    tag.async = true;
    const firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    window.onYouTubeIframeAPIReady = apiReady;
}

const initPlayer = (elemId, videoId)=>{
    new YT.Player(elemId, {
        height: '140',
        width: '100%',
        videoId: videoId,
        events: {
            'onReady': onPlayerReady
        }
    });
}

const onPlayerReady = (evt) => {
    evt.target.playVideo();
}

const videoTemplate = videoName => {
    return `
            <video class='video' poster='/video/${videoName}.jpg' autoplay muted loop width='1920' height='800'>
                <source src='/video/${videoName}.mp4' type='video/webm'/>
                <source src='/video/${videoName}.webm' type='video/mp4'/>
            </video>
            `;
};

(function () {
    [...utils.getElems('.js-video')].forEach(videoBox => {        
        videoBox.innerHTML = videoTemplate(videoBox.dataset.video);
    });
}());

loadVideoApi();

export {
    initPlayer,
    loadVideoApi
}


