const utils = {
    getElem: (selector, context) => {
        let ctx = context || document;
        return ctx.querySelector(selector);
    },
    getElems: (selector) => {
        return document.querySelectorAll(selector);
    },
    globals: {
        window: $(window),
        doc: $('html'),
        body: $('body')
    },
    setSvgIcon: (name, width, height) => {
        width = width || 30;
        height = height || 30;
        return `<svg class="svg-icon" width="${width}" height="${height}"><use xlink:href="#${name}" aria-hidden="true"/></svg>`;
    },
    scrollTo: (id) => {
        console.log(id)
        $('body,html').animate({
            scrollTop: $(id).offset().top - $('.js-header').innerHeight()
        }, 500);
    },
};

export default utils;


