import utils from "../utils/utils";
import { sliderOpts } from "./sliders";
import "../vendor/baron.min";

const $doc = utils.globals.doc;
const $sliderModal = $("#slider-modal");
const productImages = $(".js-product-image");
const $modalMainSlider = $(".js-modal-slider");
const thanks = utils.getElem("#thanks");
let $thanks;
if (thanks) {
  $thanks = $(thanks.content.children[0].cloneNode(true));
}

const changeModalBox = ($boxToHide, $boxToShow) => {
  $boxToHide.animate(
    {
      opacity: 0
    },
    200,
    function() {
      $boxToShow.css({
        opacity: 0,
        top: -10
      });

      $(this)
        .hide()
        .after($boxToShow);

      $boxToShow.animate(
        {
          opacity: 1,
          top: 0
        },
        200
      );
    }
  );
};
const setImages = function() {
  if ($(window).width() > 767) {
    productImages.each(function() {
      const $s = $(this);
      $s.attr("src", $s.data("lgImg"));
    });
  } else {
    productImages.each(function() {
      const $s = $(this);
      $s.attr("src", $s.data("smImg"));
    });
  }
};

if (productImages.length) {
  $doc.ready(setImages);
  utils.globals.window.on("resize orientationchange", setImages);
}

$modalMainSlider.on("afterChange", function() {
  $modalMainSlider.addClass("in");
});

$modalMainSlider.slick({
  dots: false,
  fade: true,
  arrows: true,
  autoplay: false
});

$sliderModal.on("shown.bs.modal", function(e) {
  $modalMainSlider
    .find(".js-modal-inner-slider")
    .each(function() {
      const $this = $(this);
      if (!$this.hasClass("slick-initialized")) {
        $this.slick(
          Object.assign({}, sliderOpts, {
            fade: true,
            autoplay: false
          })
        );
      }

      $this.slick("slickGoTo", 0);
    })
    .end()
    .addClass("in");
});

$sliderModal.on("hidden.bs.modal", function(e) {
  $sliderModal.find(".js-product-modal-form").remove();
  $sliderModal.find(".js-thanks").remove();
  $sliderModal.find(".js-product-modal-content-inner").css({
    display: "",
    opacity: 1
  });
});

$doc.on("click", ".js-product", function(e) {
  e.preventDefault();
  $(".js-product").removeClass("isActive");

  $(this).addClass("isActive");

  if ($modalMainSlider.hasClass("slick-initialized")) {
    $modalMainSlider.slick(
      "slickGoTo",
      parseFloat($(".js-product.isActive").data("index"))
    );
  }
  $sliderModal.modal("show");
});

$doc.on("click", ".js-prod-popup-video", function(e) {
  e.preventDefault();
  $sliderModal.modal("hide");
});

$doc.on("click", ".js-product-modal-order", function(e) {
  e.preventDefault();
  changeModalBox(
    $(this).parents(".js-product-modal-content-inner"),
    $(utils.getElem("#product-modal-form").content.children[0].cloneNode(true))
  );
});

$doc.on("submit", ".js-product-modal-form", function(e) {
  e.preventDefault();
  changeModalBox(
    $(this)
      .parents(".js-product-modal-content")
      .find(".js-product-modal-form"),
    $thanks
  );
});

Array.prototype.forEach.call(utils.getElems(".js-scroll-y"), elem => {
  baron({
    root: elem,
    scroller: ".baron__scroller",
    bar: ".baron__bar",
    scrollingCls: "_scrolling",
    draggingCls: "_dragging"
  });
});

export { changeModalBox };



