import utils from "../utils/utils";
import { loyaltySliderOpts, sliderOpts } from "./sliders";
import { initPlayer } from "../devs/video";

const $doc = utils.globals.doc;
const $modal = $("#modal-video");
const $videoContainer = $modal.find(".js-modal-video");

$doc.ready(function($) {
  $doc.on("click", ".js-popup-link", function(e) {
    e.preventDefault();
    $($(this).attr("href")).modal("show");
  });

  $doc.on("click", ".js-blog-modal-link", function(e) {
    e.preventDefault();
    const modalContent = $("#modal-blog").find(".js-post-content");
    const content = $($(this).data("content-id"))
      .find(".js-article-content")
      .clone();
    modalContent.empty();
    modalContent.append(content);
    $("#modal-blog").modal("show");
  });

  $doc.on("click", ".js-popup-close", function(e) {
    e.preventDefault();
    $(
      `#${$(this)
        .parents(".modal")
        .attr("id")}`
    ).modal("hide");
  });

  $doc.on("click", ".js-popup-video", function(e) {
    e.preventDefault();
    const $this = $(this);
    const $videoElem = $('<div id="js-modal-video">');

    if (window.YtApiReady) {
      $videoContainer.empty();

      $videoContainer.append($videoElem);

      initPlayer("js-modal-video", $this.data("video"));
    }

    $modal.modal("show");
  });

  $modal.on("hidden.bs.modal", function(e) {
    $videoContainer.empty();
  });

  const gallerySlider = $(".js-gallery-slider");
  $doc.on("click", ".js-gallery-image", function() {
    // $("#slider-gallery").modal("show");
    const idx = Number(
      $(this)
        .parents(".slick-slide")
        .data("slick-index")
    );

    if (!gallerySlider.hasClass("slick-initialized")) {
      gallerySlider.slick(
        Object.assign({}, loyaltySliderOpts, {
          appendArrows: gallerySlider,
          dots: false,
          responsive: [
            {
              breakpoint: 600,
              settings: {
                slidesToShow: 1,
                arrows: true
              }
            }
          ]
        })
      );
    } else {
    }

    $("#slider-gallery").modal("show");
    gallerySlider.slick("slickGoTo", idx, false);

    setTimeout(() => {
      $(".gallery-slider").css({
        opacity: 1
      });
    }, 500);
  });

  $doc.on("click", ".js-complect", function(e) {
    e.preventDefault();
    const $this = $(this);

    $("#modal-complects").modal("show");

    window.openedIdx = parseFloat($this.data("index"));
  });

  $("#modal-complects").on("hidden.bs.modal", function(e) {
    $(".modal-complects .modal-dialog").css({
      opacity: 0
    });
  });

  $("#modal-complects").on("shown.bs.modal", function() {
    let idx = window.openedIdx ? window.openedIdx : 2;
    $(".js-modal-complect-top-slider").each(function() {
      if (!$(this).hasClass("slick-slider")) {
        $(this).slick(
          Object.assign({}, sliderOpts, {
            fade: true
          })
        );
      }
      $(this).slick("slickGoTo", 1);
    });
    if (!$(".js-modal-complect-slider").hasClass("slick-slider")) {
      $(".js-modal-complect-slider").slick(
        Object.assign({}, sliderOpts, {
          fade: true,
          dots: false,
          arrows: true,
          swipe: false,
          autoplay: false,
          adaptiveHeight: true
        })
      );
    }

    $(".js-modal-complect-slider").slick("slickGoTo", idx - 1);

    $(".modal-complects .modal-dialog").css({
      opacity: 1
    });
  });
});



