// import $ from 'jQuery';
import utils from "./utils/utils";
import pageWidget from "./utils/page-widget";
import bootstrap from "bootstrap";
import "jquery-nice-select";
import "./vendor/sprite-svg";
import "./devs/popups";
import "./devs/video";
import "./devs/map";
import "./devs/sliders";
import "./devs/shop";
import "./vendor/baron.min";
import { orderSliderOpts } from "./devs/sliders";
import { changeModalBox } from "./devs/shop";

const $doc = utils.globals.doc;
const $body = utils.globals.body;

pageWidget([
  ["index", "Головна"],
  ["contacts", "Контакти"],
  ["quest-room", 'Quest room "Witcher"'],
  ["battle-room", "Battle Room"],
  ["loyalty", "Програма лояльності"],
  ["children", "Дитячі свята"],
  ["faq", "FAQ"],
  ["shop", "Магазин"],
  ["blog", "Блог"]
]);

$doc.ready(function($) {
  $(function() {
    $('[data-toggle="tooltip"]').tooltip();
  });

  $("select").niceSelect();

  // tab nav fix
  $doc.on("click", '[data-toggle="tab"]', function() {
    const $this = $(this);
    $this
      .parents("ul")
      .find("li")
      .removeClass("active");
    $this.parent().addClass("active");
  });
  // tab nav fix

  // nav
  $doc.on("click", ".js-open-menu", e => {
    $body.addClass("navOpened");
  });

  $doc.on("click", ".js-close-menu", e => {
    $body.removeClass("navOpened");
  });
  // nav

  // page nav
  const pageNav = $(".js-page-nav");
  $doc.on("click", ".js-page-nav-link", e => {
    e.preventDefault();
    const targetSectionId = $(e.target).attr("href");
    // console.log(targetSectionId)
    if ($(targetSectionId).length) {
      utils.scrollTo(targetSectionId);
      switchActiveNavElem(targetSectionId);
    }
  });

  function switchActiveNavElem(id) {
    $(".js-page-nav-link").removeClass("isCurrent");
    $(`[href="${id}"]`).addClass("isCurrent");
  }

  const scrollNavHandler = e => {
    const sections = utils.getElems(".js-nav-section");
    const windowHeight = document.documentElement.clientHeight;

    // const videoBox = utils.getElem('.js-video-box');
    // const videoBoxRect = videoBox.getBoundingClientRect();
    // const navListRect = utils.getElem('.js-page-nav-list').getBoundingClientRect();

    // if (navListRect.top < videoBoxRect.bottom) {
    //     pageNav[0].classList.add('switchColor');
    // } else {
    //     pageNav[0].classList.remove('switchColor');
    // }
    Array.prototype.forEach.call(sections, section => {
      const rect = section.getBoundingClientRect();
      if (rect.top < windowHeight / 2 && rect.top > 0) {
        switchActiveNavElem(`#${section.id}`);
        console.log(section.id);
      }
    });
  };

  if (pageNav.length) {
    scrollNavHandler();
    window.addEventListener("scroll", scrollNavHandler);

    window.addEventListener("resize", scrollNavHandler);
  }
  // page nav

  // video paralax
  //   const paralaxBox = utils.getElem(".js-paralax");
  //   const makeParalax = e => {
  //     const paralaxBox = utils.getElem(".js-paralax");
  //     const paralaxBoxRect = paralaxBox.getBoundingClientRect();
  //     paralaxBox.style.transform = `translateY(${-1 *
  //       paralaxBoxRect.top /
  //       1.5}px)`;
  //   };

  //   if (paralaxBox) {
  //     window.addEventListener("scroll", makeParalax);
  //   }
  // video paralax

  // anchor
  (function() {
    $doc.on("click", ".js-anchor-link", e => {
      e.preventDefault();
      const targetId = $(e.target).attr("href");
      if ($(targetId).length) {
        utils.scrollTo(targetId);
        $("body").removeClass("navOpened");
      }
    });
  })();

  (function() {
    const anc = $(window.location.hash);
    if (anc.length) {
      utils.scrollTo(window.location.hash);
    }
  })();
  // anchor

  // regimes hover
  const initSpoilers = $elems => {
    $elems.each(function() {
      const s = $(this);
      const scrollHeight = s.prop("scrollHeight");

      s.css({
        "max-height": 0
      });
      s.data("scroll-height", scrollHeight);
    });
  };
  const regimes = $(".js-regime");
  if (regimes.length) {
    initSpoilers(regimes.find(".js-regime-more"));
  }
  regimes.hover(
    function() {
      const more = $(this).find(".js-regime-more");
      more.animate(
        {
          "max-height": parseFloat(more.data("scroll-height"))
        },
        300
      );
    },
    function() {
      const more = $(this).find(".js-regime-more");
      more.animate(
        {
          "max-height": "0"
        },
        300
      );
    }
  );
  // regimes hover

  // Order

  const tabMove = function(e) {
    e.preventDefault();
    const orderTabs = $(".js-order-tabs").find("li");
    const activeTabIndex = orderTabs.index(
      $(".js-order-tabs").find("li.active")
    );

    if ($(this).attr("href") === "#next") {
      const next = $(orderTabs[activeTabIndex + 1]);
      if (next.length) {
        next.find("a").click();
      } else {
        orderTabs
          .first()
          .find("a")
          .click();
      }
    } else {
      const prev = $(orderTabs[activeTabIndex - 1]);
      if (prev.length) {
        prev.find("a").click();
      } else {
        orderTabs
          .last()
          .find("a")
          .click();
      }
    }
  };

  const reinitOrderSlider = function(e) {
    const orderSlider = $($(e.target).attr("href")).find(".js-order-slier");
    if (orderSlider.length) {
      if (orderSlider.hasClass("slick-initialized")) {
        orderSlider.slick("unslick");
        orderSlider.slick(orderSliderOpts);
      } else {
        orderSlider.slick(orderSliderOpts);
      }
    }
  };

  const openOrderModal = e => {
    const $orderModal = $("#modal-order");
    $orderModal.modal("show");
  };
  (function() {
    // $doc.on("click", ".js-order-aside-link", tabMove);
    $('a[data-toggle="tab"]').on("shown.bs.tab", reinitOrderSlider);
    $doc.on("change", ".js-order-input", openOrderModal);
  })();

  //scroller
  (function() {
    $(".js-faq-nav-item")
      .first()
      .addClass("isActive");

    utils.globals.doc.on("click", ".js-faq-nav-next", function() {
      scroll(210);
    });
    utils.globals.doc.on("click", ".js-faq-nav-prev", function() {
      scroll(-210);
    });

    const scroller = utils.getElem(".main__clipper");

    if (scroller) {
      baron({
        root: ".main__clipper",
        scroller: ".main__scroller",
        bar: ".main__bar",
        scrollingCls: "_scrolling",
        draggingCls: "_dragging",
        direction: "h"
      });
    }
  })();

  $doc.on("click", function(e) {
    if ($(e.target).parents(".js-faq-nav-item").length != 1) {
      $(".js-faq-menu").removeClass("isVisible");
    }
  });
  // $('.js-faq-nav-item').on('mouseenter click', function () {
  //     const $self = $(this);
  //     const menu = $($self.data('nav'));
  //     const boxRect = $('.js-faq-nav')[0].getBoundingClientRect();
  //     const selfRect = $self[0].getBoundingClientRect();
  //     let left = selfRect.left;
  //     if (selfRect.left < boxRect.left) {
  //         left = boxRect.left
  //     }
  //     $('.js-faq-menu').removeClass('isVisible');
  //     if (left + menu.innerWidth() > $(window).width()) {

  //     }

  //     if (left + menu.innerWidth() > $(window).width()) {
  //         menu.css({
  //             left: `auto`,
  //             right: 7
  //         }).addClass('isVisible');
  //     } else {
  //         menu.css({
  //             left: `${left}px`,
  //             right: 'auto'
  //         }).addClass('isVisible');
  //     }
  // })

  $(".js-show-info").on("click", function(e) {
    e.preventDefault();
    $(".js-loader").addClass("isLoading");
    const tabToShow = $($(this).data("tab"));
    if (!$(this).hasClass("js-faq-nav-item")) {
      $(".js-faq-menu").removeClass("isVisible");
    }
    setTimeout(function() {
      $(".js-faq-tab").removeClass("isVisible");
      tabToShow.addClass("isVisible");
      $(".js-loader").removeClass("isLoading");
    }, 300);
  });

  $(window).on("scroll orientationchange", function() {
    $(".js-faq-menu").removeClass("isVisible");
  });

  function scroll(px) {
    $(".js-scroll-canvas").animate(
      {
        scrollLeft: $(".js-scroll-canvas").scrollLeft() + px
      },
      500
    );
  }

  $(".js-acordeon")
    .not(".isOpened")
    .not(".isDisabled")
    .find(".js-acordeon-body")
    .hide();

  utils.globals.doc.on("click", ".js-acordeon-heading", function(e) {
    e.preventDefault();
    const currentItem = $(this).parents(".js-acordeon");

    if (currentItem.hasClass("isDisabled")) return;

    // $('.js-accordion')
    //   .not(currentItem)
    //   .find('.js-accordion-body')
    //   .slideUp()
    //   .removeClass('.isOpened');

    currentItem
      .find(".js-acordeon-body")
      .slideToggle()
      .end()
      .toggleClass("isOpened");
  });

  $doc.on("submit", ".js-order-form", function(e) {
    e.preventDefault();
    const thanks = utils.getElem("#order-thanks");
    let $thanks;
    if (thanks) {
      $thanks = $(thanks.content.children[0].cloneNode(true));
    }
    changeModalBox($(this).find(".js-order-controls"), $thanks);
  });
});



